import React, { Component } from 'react'
import Avatar from '../../images/about-avatar.jpg'
import Link from 'gatsby-link'
class Author extends Component {
  constructor(props) {
    super(props)
  }

  render() {
    return (
      <div className="author-wrapper">
        <div className="author-avatar">
          <img src={Avatar} alt="Phanor Coll" />
        </div>
        <div className="author-data">
          <h2>Phanor Coll</h2>
          <p>
            With more than 20 years of experience, I love to learn every day,
            hack around and bring crazy ideas to life. Currently immersed in the
            world of <span className="standout">Go(golang)</span>,{' '}
            <span className="standout">Graph databases(Neo4j, Dgraph)</span> and{' '}
            <span className="standout">Rectjs</span>.
          </p>
          <Link to="/about" className="a-animation">Learn more about me..</Link>
        </div>
      </div>
    )
  }
}

export default Author



